<template>
    <div style="position: absolute;top:0;bottom:0;left:0;right:0;overflow:auto;">
        <div class="loginNavFix">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">帖子详情</p>
            <p class="navBtn f24 cfff pl-30" @click="approval" v-if="$route.query.type == 1 && $route.query.state == 1">审核</p>
        </div>
        <div class="setBox">
            <div class="title-detail pl-30 f28 c333 pt-20 pb-30">{{data.title}}</div>
            <p class="f18 c999 pl-30">{{data.createTime | fileTime(data.createTime)}}</p>
            <div class="borderBo c666 f26 pl-30 pt-40 pr-30 pb-40">{{data.topicDesc}}</div>
            <div class="upload">
                <p class="pl-30 f28 c333 pt-20 pb-30">图片</p>
                <div class="iploadBox">
                    <div class="imgShowBox" v-for="(item,index) in data.images" :key="index">
                        <div class="imgShow">
                            <img :src="$api.fileUrl + item" alt="">
                        </div>
                    </div>
                </div>
            </div>
            <div class="upload mt-30">
                <p class="pl-30 f28 c333 pt-20 pb-30">视频</p>
                <div class="iploadBox">
                    <div class="imgShowBox" v-for="(item,index) in data.videos" :key="index">
                        <div class="imgShow">
                            <video :src="$api.fileUrl + item" alt=""></video>
                        </div>
                    </div>
                    
                </div>
            </div>
            <div class="messageBtn" v-if="!($route.query.type == 1 && $route.query.state == 1)">
                <div class="teams" @click="insertNewsClassesPraise">
                    <img v-if="data.type == 1" src="../../../assets/index/zan.png" alt="">
                    <img v-else src="../../../assets/index/nozan.png" alt="">
                    <p>{{data.praiseNum}}</p>
                </div>
                <div class="teams" @click="wirteMessage">
                    <img src="../../../assets/index/operat.png" alt="">
                    <p>{{data.replyCount}}</p>
                </div>
            </div>
            <div class="comment" v-if="!($route.query.type == 1 && $route.query.state == 1)">
                <div class="shuxian l"></div>
                <h2 class="commTotle l">最新评论</h2>
                <span class="commNum">({{list.length}})</span>
                <div class="more r" @click="moreComment">
                    <p>更多</p>
                    <img src="../../../assets/my/more.png" alt="">
                </div>
            </div>
            <div class="comm-for" v-if="!($route.query.type == 1 && $route.query.state == 1)">
                <div class="comm-list" v-for="(item,index) in list" :key="index">
                    <img class="l" :src="$api.fileUrl + item.headImage" alt="">
                    <div class="comm-right l">
                        <div class="comm-title">
                            <div class="comm-name l">{{item.teacherName}}</div>
                            <span class="r">{{item.create_time | fileTime(item.create_time)}}</span>
                        </div>
                        <div class="comm-content">{{item.content}}
                        </div>
                    </div>
                </div>
            </div>
            <div class="inputFixed">
                <input class="l" v-model="content" type="text" ref="oinp">
                <div class="l sendBtn" @click="insertClassReply">发送</div>
            </div>
        </div>
    </div>
</template>

<script>
import {publisFn} from '../../../lib/util'
export default {
  name: 'HelloWorld',
    data () {
        return {
            data:{
                createTime:'',
                images:[],
                praiseNum:0,
                replyCount:0,
                title:'',
                topicDesc:'',
                videos:[],
                type:''
            },
            list:[],
            content:''
        }
    },
    filters : {
        fileTime(num){
           return publisFn.dateFormat(num)
        }
    },
    methods : {
       returns(){
            this.$router.push({path:'/classcircle'});//返回上一层
        },
        deleteThis(){
            alert('删除')
        },
        approval(){
            this.$dialog.confirm({
                title: '提示',
                message: '是否同意该家长发布信息？',
                confirmButtonText:'同意',
                cancelButtonText:'不同意',
                className:'commentApproval'
            }).then(() => {
                
            }).catch(() => {
            // on cancel
            });
        },
        wirteMessage(){
            this.show = true;
        },
        moreComment(){
            this.$router.push({path:'/morecomment',query:{id:this.$route.query.id,type:this.$route.query.type,state:this.$route.query.state}});
        },
        insertNewsClassesPraise(){//点赞
            var param = {
                topicId:this.$route.query.id,
                submitId:JSON.parse(localStorage.getItem('zhxy_user')).id,
                submitType : 2
            };
            this.$http({url:this.$api.class.insertNewsClassesPraise},param).then(data => {
                console.log(data);
                if(data.code == 0){
                    // this.data = data.data;
                    this.selectClassesTopicDetails();
                }else{
                    this.$toast(data.msg);
                }
            },err => {
                this.$toast('出错了');
            })
        },
        onConfirm(value, index) {
            this.$toast(`当前值：${value}, 当前索引：${index}`);
            this.modelShow = !this.modelShow;
            this.chooseClass = value;
        },
        onCancel(picker, value, index) {    
            this.modelShow = !this.modelShow;
        },
        onConfirm1(value, index) {
            this.$toast(`当前值：${value}, 当前索引：${index}`);
            this.modelShow1 = !this.modelShow1;
            this.chooseSubject = value;
        },
        onCancel1(picker, value, index) {    
            this.modelShow1 = !this.modelShow1;
        },
        tuenTOedit(){//跳转到下一个页面
            // this.$router.push({path:'/homeworkedit'})
        },
        selectClassesTopicDetails(){//获取班级圈详情
            this.$http({url:this.$api.class.selectClassesTopicDetails},{classesTopicId:this.$route.query.id}).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.data = data.data;
                    // this.$router.push({path:'/classcircle'});
                }else{
                    this.$toast(data.msg);
                }
            },err => {
                this.$toast('出错了');
            })
        },
        getClassReply(){//班级圈查看评论
            this.$http({url:this.$api.class.getClassReply},{replyTopicId:this.$route.query.id}).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.list = data.data;
                }else{
                    this.$toast(data.msg);
                }
            },err => {
                this.$toast('出错了');
            })
        },
        insertClassReply(){//发表回复
            if(this.content == ''){
                this.$toast('请输入回复内容');
                return
            }
            var param = {
                content:this.content,
                topicId:this.$route.query.id,
                replyId:0,
                submitId:JSON.parse(localStorage.getItem('zhxy_user')).id,
                submitType:2
            };
            this.$http({url:this.$api.class.insertClassReply},param).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.$toast('评论成功');
                    this.content = '';
                    this.getClassReply();
                    this.selectClassesTopicDetails();
                }else{
                    this.$toast(data.msg);
                }
            },err => {
                this.$toast('出错了');
            })
        }
    },
    created() {
        this.selectClassesTopicDetails();
        if(!(this.$route.query.type == 1 && this.$route.query.state == 1)){
            this.getClassReply();
        }
    },
}
</script>

<style scoped>
.setBox{position: absolute;top: 0.84rem;left: 0;right: 0;bottom: 1rem;overflow-y: auto;}
.setTitle{height: 1.11rem;border-bottom: 1px solid #f2f2f2;line-height: 1.11rem;overflow: hidden;}
.borderBo{border-bottom: 1px solid #f2f2f2;}
.oinp{float: right;border: 0;outline: 0;width: 80%;text-align: right;padding-right: 0.2rem}
.home-comntent{height: 3.74rem;border-bottom: 1px solid #f2f2f2;}
.textarea{width: 100%;height: 1.1rem;outline: 0;border: 0;box-sizing: border-box;resize:none;}
.iploadBox{margin-left: 0.3rem;overflow: hidden;}
.imgShowBox{width: 20%;float: left;margin-bottom: 0.3rem;}
.imgShow{width: 1.2rem;height: 1.2rem;border-radius: 5px;overflow: hidden;float: left;position: relative;}
.imgShow img{width: 100%;height: 100%;display: block;}
.imgShow video{width: 100%;height: 100%;display: block;}
.closeX{position: absolute;top: 0;right: 0;padding: 0.05rem 0.1rem;}
.borderTop{border-top: 1px solid #f2f2f2;}
.msgTo{width: 0.3rem;height: 0.3rem;float: right;margin: 0.35rem 0.3rem 0 0;}
.centerBtn{height: 0.9rem;width: 6.9rem;border-radius: 0.9rem; background: linear-gradient(to bottom right, #82cbff , #7b7ceb);line-height: 0.9rem; box-shadow: 0 0 10px #f0f9ff;position: fixed;bottom: 0.6rem;left: 50%;margin-left: -3.45rem; }
.messageBtn{width: 100%;height: 0.89rem;overflow: hidden;border-top: 1px solid #f2f2f2;}
.teams{float: right;overflow: hidden;margin-right: 0.3rem;padding: 0.3rem 0;}
.teams img{width: 0.3rem;height: 0.3rem;display: block;float: left;}
.comment{padding: 0.3rem;border-top: 0.1rem solid #f2f2f2;overflow: hidden;}
.teams p{color: #959595;float: left;font-size: 0.16rem;line-height: 0.3rem;margin-left: 0.13rem;}
.shuxian{width: 0.06rem;height: 0.32rem;background: #6b77ff;margin-top: 0.12rem;}
.commTotle{font-size: .3rem;color: #333;padding: 0.1rem 0;margin-left: 0.16rem}
.commNum{font-size: .22rem;color: #333;margin-top: 0.16rem;float: left;font-weight: bold;}
.comment img{width: 0.3rem;height: 0.3rem;display: block;float: left;}
.more{padding: 0.1rem;}
.more{overflow: hidden;}
.more p{float: left;font-size: 0.22rem;color: #666666;}
.comm-for{padding: 0 0.3rem;}
.comm-list{overflow: hidden;border-bottom: 1px solid #f2f2f2; }
.comm-list img{width: 0.7rem;height: 0.7rem;display: block;border-radius: 50%;}
.comm-right{margin-left: 0.3rem;width: 5.8rem;}
.comm-name{font-size: 0.26rem;color: #333333;}
.comm-title{margin-top: 0.2rem;overflow: hidden;}
.comm-content{width: 100%;min-height: 0.49rem;border-radius: 3px;background: #f3f3f3;padding: 0.18rem;box-sizing: border-box;font-size: 0.2rem;line-height: 0.36rem;margin: 0.35rem 0 0.25rem 0;}
.inputFixed{width: 100%;height: 1rem;position: fixed;left:0;bottom: 0;z-index: 999;padding: 0.2rem 0.3rem;box-sizing: border-box;}
.inputFixed input{width: 76%;height: 0.6rem;display: block;border-radius: 0.6rem;border:1px #f2f2f2 solid;padding: 0 0.2rem;}
.sendBtn{background: #6b77ff;color: #fff;border-radius: 0.6rem;width: 1rem;height: 0.6rem;text-align: center;line-height: 0.6rem;margin-left: 0.2rem}
</style>




